:root {
  --content-padding-desktop: 20px;
  --content-width-desktop: calc(1200px - var(--content-padding-desktop) * 2);
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-regular.woff2") format("woff2");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-medium.woff2") format("woff2");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: Roboto;
  src: url("../fonts/roboto-bold.woff2") format("woff2");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
input[type="file"].visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes fade-out {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
  }
}
@keyframes slide-from-left {
  0% {
    transform: translate(calc(-1010vw), -50%);
  }
  100% {
    transform: translate(-50%, -50%);
    visibility: visible;
  }
}
@keyframes slide-to-left {
  0% {
    transform: translate(-50%, -50%);
    visibility: visible;
  }
  100% {
    transform: translate(calc(-1010vw), -50%);
  }
}
@keyframes slide-from-right {
  0% {
    transform: translate(50vw, -50%);
  }
  100% {
    transform: translate(-50%, -50%);
    visibility: visible;
  }
}
@keyframes slide-to-right {
  0% {
    transform: translate(-50%, -50%);
    visibility: visible;
  }
  100% {
    transform: translate(50vw, -50%);
  }
}
