}
.form-input:focus {
  border-color: #000000;
  outline: none;
}
.form-input:focus:invalid,
.form-input:not(:placeholder-shown):not(:focus):invalid {
  color: #e74246;
  border-color: #e74246;
}
.form-input::placeholder {
  color: #444444;
}
.form-textarea {
  min-height: 118px;
  resize: vertical;
}
.form-catalog-label {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 37px;
  min-height: 25px;
  line-height: 20px;
}
.form-catalog-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-repeat: no-repeat;
  opacity: 0.4;
  transition: opacity 0.3s linear;
}
.form-catalog-label:hover::before {
  opacity: 1;
}
.form-catalog-radio__label::before {
  width: 26px;
  height: 26px;
  background-image: url("../images/icons/radio-off.svg");
  background-size: 25px 25px;
  background-position: 0% 50%;
}
.form-catalog-radio__radio:checked + .form-catalog-radio__label::before {
  background-image: url("../images/icons/radio-on.svg");
}
.form-catalog-radio__radio:disabled + .form-catalog-radio__label {
  opacity: 0.1;
  cursor: not-allowed;
}
.form-catalog-radio__radio:focus + .form-catalog-radio__label::before {
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #000000;
}
.form-catalog-checkbox__label::before {
  width: 23px;
  height: 23px;
  background-image: url("../images/icons/checkbox-off.svg");
  background-size: 23px 23px;
  background-position: 0% 50%;
}
.form-catalog-checkbox__checkbox:checked + .form-catalog-checkbox__label::before {
  width: 27px;
  height: 23px;
  background-image: url("../images/icons/checkbox-on.svg");
  background-size: 27px 23px;
}
.form-catalog-checkbox__checkbox:disabled + .form-catalog-checkbox__label {
  opacity: 0.1;
  cursor: not-allowed;
}
.form-catalog-checkbox__checkbox:focus + .form-catalog-checkbox__label::before {
  box-shadow: 0 0 0 2px #ffffff, 0 0 0 3px #000000;
}
