  outline: none;
}
.sort-type {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding-left: 0;
  list-style: none;
  width: 300px;
}
.sort-type__item {
  margin-right: 25px;
}
.sort-type__link {
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.3);
  transition: color 0.3s linear;
}
.sort-type__link:active,
.sort-type__link--active {
  color: #000000;
}
.sort-type__link:hover:not(.sort-type__link--active) {
  color: rgba(0, 0, 0, 0.6);
}
.sort-direction {
  display: flex;
  justify-content: flex-end;
  margin: 0;
  padding-left: 0;
  list-style: none;
  width: 60px;
}
.sort-direction__item {
  margin-left: 19px;
}
.sort-direction__link {
  display: block;
  width: 0;
  height: 0;
  border-left: 5.5px solid transparent;
  border-right: 5.5px solid transparent;
  border-bottom: 10px solid rgba(0, 0, 0, 0.2);
  color: currentColor;
  transition: border-bottom-color 0.3s linear;
}
.sort-direction__link:active,
.sort-direction__link--active {
  border-bottom-color: #000000;
}
.sort-direction__link:hover:not(.sort-direction__link--active) {
  border-bottom-color: #a6a6a6;
}
.sort-direction__link--descending {
  transform: rotate(180deg);
}
.catalog {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  width: 760px;
}
.catalog__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 70px;
}
.catalog__title {
  margin: 0;
  width: 360px;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: #000000;
  text-transform: uppercase;
}
.catalog__sort-type {
  margin-left: auto;
}
.catalog__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
  row-gap: 30px;
  margin: 0;
  padding-left: 0;
  list-style: none;
  margin-top: 32px;
  margin-bottom: 58px;
}
.card-product {
  position: relative;
  width: 360px;
  height: 618px;
  border-top: 40px solid rgba(68, 68, 68, 0.12);
  border-radius: 5px;
  transition: 0.6s ease-in-out;
}
.card-product:hover {
  border-top-color: #444444;
  box-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.25);
}
.card-product::before {
  content: "";
  position: absolute;
  left: 16px;
  top: -27px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #ffffff;
  box-shadow: 23px 0 0 #ffffff, 46px 0 0 #ffffff;
}
.card-product__image {
  display: block;
  width: 100%;
  height: 578px;
  object-fit: cover;
}
.card-product__description {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 8px 60px 42px;
  text-align: center;
  background-color: #eeeeee;
  opacity: 0;
  transition: opacity 0.6s ease-in-out;
}
.card-product:hover .card-product__description {
  opacity: 1;
}
.card-product:focus-within .card-product__description {
  opacity: 1;
}
.card-product__title {
  margin-top: 16px;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  color: #000000;
  text-transform: uppercase;
}
.card-product__link {
  color: #000000;
  text-decoration: none;
}
.card-product__link:hover {
  color: #fb565a;
}
.card-product__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.card-product__text {
  overflow: hidden;
  max-height: 380px;
  line-height: 18px;
  color: #666666;
}
.card-product__button {
  margin-top: 18px;
  width: 200px;
}
.pagination {
  display: flex;
  margin: 0;
  padding-left: 0;
  list-style: none;
}
.pagination__item {
  margin-right: 12px;
}
.pagination__button {
  padding: 16px 20px;
}
.pagination__button--current {
  background-color: #ffffff;
  box-shadow: inset 3px 3px 0 #dbdbdb, inset -3px -3px 0 #dbdbdb;
}
.pagination__button--current:hover {
  background-color: #ffffff;
}
.pagination__button--current:active {
  color: #000000;
  background-color: #ffffff;
  box-shadow: inset 3px 3px 0 #dbdbdb, inset -3px -3px 0 #dbdbdb;
}
.pagination__button-next {
  padding: 16px 78px;
}
